.p_features .row:nth-child(odd){
    background-color: var(--bs-gray-100);
}
.p_features .row:nth-child(1){
    background-color: var(--bs-gray-100);
}

.p_features img{
    width: 14px;
    margin: 2px 5px;
}
.p_feature_head .row .col:first-child,
.p_features .row .col:first-child{
    min-width: 220px;
}
.p_feature_head .row .col,
.p_features .row .col{
    min-width: 200px;
}

.p_features .row:first-child .col{
    text-transform: uppercase;
}

// .p_features .row:nth-child(7) .col{
//     background-color: rgba(var(--bs-primary-rgb), 1);
//     color: #fff;
// }

.p_feature_head .row .col:nth-child(4),
.p_features .row .col:nth-child(4) {
    border: 2px solid #4e69ed;
    border-bottom: none;
    border-top: none;
}

.p_feature_head .row:nth-child(1) .col:nth-child(4){
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    border-top: 2px solid #4e69ed;
}
.p_features .row:last-child .col:nth-child(4) {
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    border-bottom: 2px solid #4e69ed;
}

@media (min-width: 992px) {
    .border_gradient:after {
        background-image: radial-gradient(var(--bs-gray-300), var(--bs-gray-100), var(--bs-gray-100));
        content: "";
        height: 100%;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 1px;
    }
}